import React, { useEffect, useState } from 'react';
import Chanvideo from './Chanvideo';
import { useParams } from 'react-router-dom';
import axios from 'axios';

const Chandetails = () => {
  const [data, setData] = useState([]);
  const [chandetail, setChandetail] = useState({});
  const { channelid } = useParams();

  const getData = async (options) => {
    try {
      const response = await axios.request(options);
      console.log("mydat");
      console.log(response.data);
      setData(response.data.data);
      setChandetail(response.data.meta);
    } catch (error) {
      console.error(error);
    }
  }

  useEffect(() => {
    const options = {
      method: 'GET',
      url: 'https://yt-api.p.rapidapi.com/channel/videos',
      params: {
        id: `${channelid}`
      },
      headers: {
        'X-RapidAPI-Key': 'e65e3c9f89msh679b4854cab7519p19ef50jsnde285c9d02bf',
        'X-RapidAPI-Host': 'yt-api.p.rapidapi.com'
      }
    };
    getData(options);
  }, [channelid]);

  return (
    <>
      <div className="chan-upper">
        <div className="banner">
          {chandetail.banner && chandetail.banner[0] ? <img src={chandetail.banner[0].url} alt="banner" /> : <img src ={"/images/banner.png"}/>}
        </div>
        <div className="channel-details">
          <div className="start">
            <div className="img">
              {chandetail.avatar && chandetail.avatar[0] ? <img src={chandetail.avatar[0].url} alt="profile" /> : null}
            </div>
            <div className="chandetails">
              <h2 className="title space">{chandetail.title}</h2>
              <p className='space'><span className="chanhandle">{chandetail.channelHandle}</span><span className="sub">{chandetail.subscriberCountText}</span><span className="total">{chandetail.videosCountText} videos</span></p>
              <p className="chan-description space">{chandetail.description ? chandetail.description.slice(0, 40) : null}...</p>
            </div>
          </div>
          <div className="end">
            <button>Subscribe</button>
          </div>
        </div>
      </div>
      <div className="chan-lower">
        {data ? data.map((val, i) => (
          <Chanvideo key={i} chanid={val.channelId} chantitle={val.channelTitle} vidthumb={val.thumbnail && val.thumbnail[0] ? val.thumbnail[0].url : null} vidtitle={val.title} vidid={val.videoId} vidviews={val.viewCount} vidtime={val.publishedTimeText} />
        )) : null}
      </div>
    </>
  )
}

export default Chandetails;
