import React from 'react'
import { NavLink } from 'react-router-dom'

const Card = (props) => {
    return (
        <>
            <div className="cardd">
                <div className="video-left">
                    <NavLink to={`/video/${props.vidid}/${props.chanid}`} ><img src={props.vidthumb} alt="photo" /></NavLink>
                </div>
                <div className="video-right">
                    <NavLink to={`/video/${props.vidid}/${props.chanid}`}><h3>{props.vidtitle.length > 50 ? <h3>{props.vidtitle.slice(0, 50)}...</h3> : <h3>{props.vidtitle}</h3>}</h3></NavLink>
                    <NavLink to={`/channel/${props.chanid}`} className="channel"><span>{props.chantitle}</span></NavLink>
                    <p><span className="views">{props.vidviews} views</span> <span className='time'>{props.vidtime}</span> </p>
                </div>
            </div>
        </>
    )
}

export default Card
