import React from 'react'
import Sidebar from '../Sidenavbar/Sidebar'
import Channeldetails from './Channeldetails'

const Combo = () => {
    return (
        <>
            <div id="wrapper">
                <div className="left">
                    <Sidebar />
                </div>
                <div className="right">
                    <Channeldetails/>
                </div>
            </div>
        </>
    )
}

export default Combo
