import React, { useState } from 'react'
import "./navbar.css"
import { NavLink } from 'react-router-dom'
import axios from 'axios'

const Navbar = (props) => {
  const [input, setInput] = useState("")
  const [data, setData] = useState({})

  // function to handle input
  const changeInput = (e) => {
    setInput(e.target.value)
  }

  // function to get video data
  const getVideo = async (options) => {
    try {
      const response = await axios.request(options);
      setData(response.data)
    } catch (error) {
      console.error(error);
    }
  }
  console.log(data)

  //function to handle search button
  const videoData = () => {
    if (input) {
      const options = {
        method: 'GET',
        url: 'https://yt-api.p.rapidapi.com/search',
        params: { query: `${input}` },
        headers: {
          'X-RapidAPI-Key': 'e65e3c9f89msh679b4854cab7519p19ef50jsnde285c9d02bf',
          'X-RapidAPI-Host': 'yt-api.p.rapidapi.com'
        }
      };
      getVideo(options)

    } else {
      return
    }
  }

  return (
    <>
      <nav id='container'>
        <div className="start">
          <button><i className="fa-solid fa-bars"></i></button>
          <NavLink to="/"><span><img src="/images/logo.png" alt='logo' /></span>YouTube</NavLink>
        </div>
        <div className="center">
          <div className="group">
            <input type="search" value={input} onChange={changeInput} placeholder='Search' />
            <button onClick={videoData}><i className="fas fa-search"></i></button>
          </div>
          <i className="fa-solid fa-microphone micro"></i>
        </div>
        <div className="end">
          <i className="fas fa-video fa-plus space"></i>
          <i className="fa-regular fa-bell space"></i>
          <img className='space' src="/images/profile.png" alt="logo" />
        </div>
      </nav>
    </>
  )
}

export default Navbar;
