import React from 'react'
import Navbar from '../Navbar/Navbar'
import { useParams } from 'react-router-dom'
import "./videospecial.css"

import Videoplayer from '../Videoplayer/Videoplayer'
import Relatedvideos from '../Relatedvideos/Relatedvideos'


const Videospecial = () => {
  return (
    <>
        <div id="specific">
            <Navbar/>
            <div id='overflowvertical' className="combination">
              <div className="left">
                  <Videoplayer/>
              </div>
              <div className="right">
                  <Relatedvideos/>
              </div>
            </div>
        </div>
    </>
  )
}

export default Videospecial
