import React, { useEffect, useState } from 'react';
import ThumbUpOutlinedIcon from '@mui/icons-material/ThumbUpOutlined';
import ThumbDownOffAltOutlinedIcon from '@mui/icons-material/ThumbDownOffAltOutlined';
import ReplyIcon from '@mui/icons-material/Reply';
import MoreHorizOutlinedIcon from '@mui/icons-material/MoreHorizOutlined';
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import DownloadIcon from '@mui/icons-material/Download';
import { NavLink, useParams } from 'react-router-dom';
import axios from 'axios';

const Videodetails = () => {
  const { videoid } = useParams();
  const { channelid } = useParams();
  const [data, setData] = useState({});
  const [chandata, setChandata] = useState({});

  // function to video details
  const videoDetails = async (options) => {
    try {
      const response = await axios.request(options);
      setData(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    const options = {
      method: 'GET',
      url: 'https://yt-api.p.rapidapi.com/video/info',
      params: { id: `${videoid}` },
      headers: {
        'X-RapidAPI-Key': 'e65e3c9f89msh679b4854cab7519p19ef50jsnde285c9d02bf',
        'X-RapidAPI-Host': 'yt-api.p.rapidapi.com',
      },
    };
    videoDetails(options);
  }, [videoid]);

  //function to get more detail of video
  const getDetails = async (options) => {
    try {
      const response = await axios.request(options);
      setChandata(response.data);
      // console.log(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    const options = {
      method: 'GET',
      url: 'https://yt-api.p.rapidapi.com/channel/about',
      params: {
        id: `${channelid}`,
      },
      headers: {
        'X-RapidAPI-Key': 'e65e3c9f89msh679b4854cab7519p19ef50jsnde285c9d02bf',
        'X-RapidAPI-Host': 'yt-api.p.rapidapi.com',
      },
    };
    getDetails(options);
  }, [channelid]);

  return (
    <>
      <div className="details">
        <h3>{data.title}</h3>
        <div className="inner-details">
          <div className="start">
            {chandata.avatar && chandata.avatar[0] ? (
              <NavLink to={`/channel/${data.channelId}`}>
                <img src={chandata.avatar[0].url} alt="profile" />
              </NavLink>
            ) :
              <img src="images/common-profile.png" alt="profile" />}
            <div className="inner-channel">
              {data.channelTitle ? (
                <>
                  <p className="name">
                    <NavLink to={`/channel/${data.channelId}`}>
                      {data.channelTitle.length > 20 ? (
                        <span>{data.channelTitle.slice(0, 20)}...</span>
                      ) : (
                        <span>{data.channelTitle}</span>
                      )}
                    </NavLink>
                  </p>
                  <p className="subscriber">
                    {chandata.subscriberCountText} subscribers
                  </p>
                </>
              ) : null}
            </div>
          </div>
          <div className="center">
            <button className="btn">
              <NotificationsNoneIcon />
              <span>Subscribe</span>
            </button>
          </div>
          <div className="end">
            <div className="btn-group">
              <button className="btn btn-space">
                <ThumbUpOutlinedIcon />
              </button>
              <button className="btn btn-space-right">
                <ThumbDownOffAltOutlinedIcon />
              </button>
            </div>
            <button className="btn">
              <ReplyIcon />
              <span>Share</span>
            </button>
            <button className="btn">
              <DownloadIcon />
              <span>Download</span>
            </button>
            <button className="btn btn-circle">
              <MoreHorizOutlinedIcon />
            </button>
          </div>
        </div>
      </div>
      <div className="description">
        <p>
          <span className="views">{data.viewCount} views</span>
          <span>{data.publishDate}</span>
        </p>
        {data.description &&
          data.description.split('\n').map((paragraph, index) => (
            <p key={index}>{paragraph}</p>
          ))}
      </div>
    </>
  );
};

export default Videodetails;
