import React from 'react'
import { NavLink } from 'react-router-dom'

const Chanvideo = (props) => {
    return (
        <>
            <div id="channel-card">
                <div className="upper">
                    <NavLink to={`/video/${props.vidid}/${props.chanid}`}><img src={props.vidthumb} alt="photo" /></NavLink>
                </div>
                <div className="lower">
                    <div className="video-right">
                        <NavLink to={`/video/${props.vidid}/${props.chanid}`}>{props.vidtitle.length > 30 ? <h3>{props.vidtitle.slice(0, 30)}...</h3> : <h3>{props.vidtitle}</h3>}</NavLink>
                        <NavLink to={`/channel/${props.chanid}`} className="channel"><span>{props.chantitle}</span></NavLink>
                        <p><span className="views">{props.vidviews} views</span> <span className='time'>{props.vidtime}</span> </p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Chanvideo
