import React from 'react'
import "./sidebar.css"
import data from './data'
import { NavLink } from 'react-router-dom'

const Sidebar = () => {

    const year = new Date().getFullYear()
    console.log(year)

    return (
        <>
            <div id="sidebar-wrapper">
                <div className="section1 special">
                    {data.section1.map((val,i)=>{
                        return <NavLink key={i} to={val.src}><span className="span">{val.icon}</span>{val.name.length>15? <span>{val.name.slice(0,15)}...</span> : val.name}</NavLink>
                    })}
                </div>

                <div className="section2 special">
                    {data.section2.map((val,i)=>{
                        return <NavLink key={i} to={val.src}><span className='span'>{val.icon}</span>{val.name.length>15? <span>{val.name.slice(0,15)}...</span> : val.name}</NavLink>
                    })}
                </div>

                <div className="section3 special">
                    {data.section3.map((val,i)=>{
                        return <NavLink key={i} to={val.src}><span className="span"><img src={val.icon} alt="noo" /></span>{val.name.length>15? <span>{val.name.slice(0,15)}...</span> : val.name}</NavLink>
                    })}
                </div>

                <div className="section4 special">
                    {data.section4.map((val,i)=>{
                        return <NavLink key={i} to={val.src}><span className='span'>{val.icon}</span>{val.name.length>15? <span>{val.name.slice(0,15)}...</span> : val.name}</NavLink>
                    })}
                </div>

                <div className="section5 special">
                    {data.section5.map((val,i)=>{
                        return <NavLink key={i} to={val.src}><span className="span">{val.icon}</span>{val.name.length>15? <span>{val.name.slice(0,15)}...</span> : val.name}</NavLink>
                    })}
                </div>

                <div className="section6">
                    <div className="start wrap">
                        {
                            data.section6.map((val)=>{
                                return <NavLink>{val}</NavLink>
                            })
                        }
                    </div>

                    <div className="center wrap">
                        {
                            data.section7.map((val)=>{
                                return <NavLink>{val}</NavLink>
                            })
                        }
                    </div>
                    <div className="end">
                        <p>© {year} Google LLC</p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Sidebar
