import React, { useRef, useState } from 'react';
import "./category.css";
// import data from './datas';

const Category = (props) => {

  const data = props.filterData

  const categoryWrapperRef = useRef(null);

  // Function for left slide
  const backward = () => {
    categoryWrapperRef.current.scrollLeft -= 200; // Adjust the scroll offset as per your requirement
  };

  // Function for right slide
  const forward = () => {
    categoryWrapperRef.current.scrollLeft += 200; // Adjust the scroll offset as per your requirement
  };

  return (
    <>
      <div id="category-wrapper" ref={categoryWrapperRef}>
        {data?<i className="fa-solid fa-angle-left left-arrow" onClick={backward}></i>:null}
        {data? data.map((val, i) => {
          return <button key={i}>{val.filter}</button>;
        }):null}
        {data?<i className="fa-solid fa-angle-right right-arrow" onClick={forward}></i>:null}
      </div>
    </>
  );
};

export default Category;
