import React, { useState } from 'react'
import "./app.css"

import Home from './components/Home/Home'
import Videospecial from './components/specific/Videospecial'
import Wrapper from './components/Channel/Wrapper'
import { Route, Routes } from 'react-router-dom'

const App = () => {
  return (
    <>
      <Routes>
        <Route path="/" exact Component={Home} />
        <Route path="/video/:videoid/:channelid" exact Component={Videospecial} />
        <Route path="/video/:videoid/:channelid" exact Component={Videospecial} />
        <Route path="/channel/:channelid" exact Component={Wrapper} />
      </Routes>
    </>
  )
}

export default App
