import React, { useEffect, useState } from 'react'
import Card from './card'
import axios from 'axios'

import "./relatedvideos.css"
import { useParams } from 'react-router-dom'

const Relatedvideos = () => {
  const [data,setData] = useState([])
  const {videoid}= useParams()

  // function to get related videos
  const getVideos = async(options)=>{
    try {
      const response = await axios.request(options);
      setData(response.data.data)
      console.log("video data")
      console.log(response.data);
    } catch (error) {
      console.error(error);
    }
  }

  useEffect(()=>{
    const options = {
      method: 'GET',
      url: 'https://yt-api.p.rapidapi.com/related',
      params: {id: `${videoid}`},
      headers: {
        'X-RapidAPI-Key': 'e65e3c9f89msh679b4854cab7519p19ef50jsnde285c9d02bf',
        'X-RapidAPI-Host': 'yt-api.p.rapidapi.com'
      }
    };
    getVideos(options)
  },[videoid])

  return (
    <>
      <div className="wrapper">
        {data? data.map((val,i)=>{
           return <Card key={i} chanid={val.channelId} chantitle={val.channelTitle} vidthumb={val.thumbnail[0].url} vidtitle={val.title} vidid={val.videoId} vidviews={val.viewCount} vidtime={val.publishedTimeText}/>
          }):null
        }
      </div>
    </>
  )
}

export default Relatedvideos
