import React, { useState } from 'react'
import "./videocontainer.css"
import Video from './Video'

const VideoContainer = (props) => {
  const rawData = props.data;
  const data= rawData? rawData.filter((val)=>{
    return val.type==="video"
  }):null;
  // console.log(data)
  return (
    <div id="video-wrapper">
      {data? data.map((val, i) => {
        return <Video key={i} chanid={val.channelId} chanthumb={val.channelThumbnail[0].url} chantitle={val.channelTitle} vidthumb={val.thumbnail[0].url} vidtitle={val.title} vidid={val.videoId} vidviews={val.viewCount} vidtime={val.publishedTimeText
} />
      }):null}
    </div>
  )
}

export default VideoContainer
